.checkround {
	position: absolute;
	top: 6px;
	left: 0;
	height: 20px;
	width: 20px;
	background-color: #fff;
	border-color: #f8204f;
	border-style: solid;
	border-width: 2px;
	border-radius: 50%;
}
/* Create the indicator (the dot/circle - hidden when not checked) */
.checkround:after {
	content: "";
	position: absolute;
	display: none;
}
.check {
	display: block;
	position: relative;
	padding-left: 25px;
	margin-bottom: 12px;
	padding-right: 0px;
	cursor: pointer;
	font-size: 15px;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	line-height: 25px;
	font-weight: 300;
}
/* Hide the browser's default checkbox */
.check input {
	position: absolute;
	opacity: 0;
	cursor: pointer;
	left: 3px;
	z-index: 1;
	top: 6px;
}
/* Create a custom checkbox */
.checkmark {
	position: absolute;
	top: 3px;
	left: 0;
	height: 18px;
	width: 18px;
	background-color: #fff;
	border-color: #333;
	border-style: solid;
	border-width: 2px;
	border-radius: 4px;
}
.check input:checked ~ .checkmark {
	background-color: #f5184e;
	border-color: #f8204f;
}
.checkmark:after {
	content: "";
	position: absolute;
	display: none;
}
.check input:checked ~ .checkmark:after {
	display: block;
}
.check .checkmark:after {
	left: 5px;
	top: 1px;
	width: 5px;
	height: 10px;
	border: solid;
	border-color: #fff;
	border-width: 0 3px 3px 0;
	-webkit-transform: rotate(45deg);
	-ms-transform: rotate(45deg);
	transform: rotate(45deg);
}
